.slide {
   top: 0;
   left: 0;
   position: absolute;
   opacity: 0;
   visibility: hidden;
   z-index: 0;

   &.active {
      opacity: 1;
      visibility: visible;
      z-index: 1;
   }

   &__image {
      &_illustration {
         width: 100%;
         border-radius: 15px;
      }
   }

   &__content {
      padding: 10px;

      &_heading {
         color: orangered;
      }

      &_description {
         margin-top: 5px;
         text-align: justify;
      }

      &_toggler {
         border: none;
         background: rgba(255, 0, 0, 0);
         cursor: pointer;
         font-weight: bold;
         color: orangered;


         &:hover {
            color: rgb(50, 50, 50);
         }
      }
   }
}