* {
   margin: 0;
   padding: 0;
   transition: 0.2s;
   cursor: default;
   outline: none;
}

.infobox {
   background: #e0e0e0;
   width: 400px;
   height: 420px;
   border-radius: 30px;
   margin: 0px auto;
   padding: 4px;
   font-family: 'Helvetica';
   border: 15px solid rgb(50, 50, 50);

   .slides {
      position: relative;
   }

   &__controls {
      padding: 5px 50px;
      display: flex;
      justify-content: space-around;
      margin-top: 390px;
      &_button {
         padding: 3px 15px;
         border-radius: 10px;
         border: 2px solid orangered;
         font-weight: bold;
         cursor: pointer;
         color: white;
         background: rgb(50, 50, 50);

         &:hover {
            color: orangered;
         }

         &:active {
            border: 2px solid rgb(50, 50, 50);
         }
      }
   }
}